.tiptap {
    /* Reset margin-top for the first child */
    :first-child {
        margin-top: 0;
    }

    /* List styles */
    ul, 
    ol {
        padding: 0 1rem;
        margin: 1.25rem 1rem 1.25rem 0.4rem;
    }

    ul li p,
    ol li p {
        margin-top: 0.25em;
        margin-bottom: 0.25em;
    }

    /* Heading styles */
    h1, 
    h2, 
    h3, 
    h4, 
    h5, 
    h6 {
        line-height: 1.1;
        margin-top: 2.5rem;
        text-wrap: pretty;
    }

    h1, 
    h2 {
        margin-top: 3.5rem;
        margin-bottom: 1.5rem;
    }

    h1 { 
        font-size: 1.4rem; 
    }

    h2 { 
        font-size: 1.2rem; 
    }

    h3 { 
        font-size: 1.1rem; 
    }

    h4, 
    h5, 
    h6 { 
        font-size: 1rem; 
    }

    /* Code and preformatted text styles */
    code {
        background-color: #E5E5E5; /* Assuming var(--purple-light) is #E5E5E5 */
        border-radius: 0.4rem;
        color: #000; /* Assuming var(--black) is #000 */
        font-size: 0.85rem;
        padding: 0.25em 0.3em;
    }

    pre {
        background: #000; /* Assuming var(--black) is #000 */
        border-radius: 0.5rem;
        color: #FFF; /* Assuming var(--white) is #FFF */
        font-family: 'JetBrainsMono', monospace;
        margin: 1.5rem 0;
        padding: 0.75rem 1rem;
    }

    pre code {
        background: none;
        color: inherit;
        font-size: 0.8rem;
        padding: 0;
    }

    blockquote {
        border-left: 3px solid #D3D3D3; /* Assuming var(--gray-3) is #D3D3D3 */
        margin: 1.5rem 0;
        padding-left: 1rem;
    }

    hr {
        border: none;
        border-top: 1px solid #DCDCDC; /* Assuming var(--gray-2) is #DCDCDC */
        margin: 2rem 0;
    }
}
